import { Howl } from "howler";

import s1 from  "./hit1.m4a?url";
import s2 from  "./hit2.m4a?url";
import s3 from  "./hit3.m4a?url";
import s4 from  "./hit4.m4a?url";
import s5 from  "./hit5.m4a?url";

var sounds = [
  s1,
  s2,
  s3,
  s4,
  s5  
];

export var howls = sounds.map(
  (s) => new Howl({
    src: s,
    sprite: {
      __default: [100, 1000],
    },
  })
);
