export class Modifiers {
  init() {
    this.startDate = new Date();

  }

  isBloodSugarLow() {
    if (this.bloodSugar <= 4) {
      return true;
    }
  }

  isBloodSugarNormal() {
    if (this.bloodSugar > 4 && this.bloodSugar < 10) {
      return true;
    }
  }

  isBloodSugarHigh() {
    if (this.bloodSugar >= 10) {
      return true;
    }
  }

  getBloodSugar(time) {
    //console.log(time, Math.sin(time));
    return Math.max((Math.cos(time * .1) * 8.14) + (1.0 + Math.sin(time * .2)) * 7, 2);

  }

  update(time) {
    this.bloodSugar = this.getBloodSugar(time);
  }
}
