import "./style.css";

//import './style.css'
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
//import '../shared/OrbitControls.js'
import * as dat from "dat.gui";

import { BasicThreeDemo } from "./BasicThreeDemo";
import { Vector3 } from "three";

import { Ball } from "./Ball";
import { Modifiers } from "./Modifiers";
import { Brick } from "./Brick";
import { Overlay } from "./Overlay";
import { Padel } from "./Padel";

// smoke


import { worldWidth, worldHeight } from "./config";
import { howls } from "./sounds";




export class App extends BasicThreeDemo {
  constructor(inoptions) {
    const options = {
      container: document.getElementById("container"),
      // length: 400,
      // width: 20,
      // roadWidth: 9,
      // islandWidth: 2,
      // nPairs: 100,
      // roadSections: 3,
      // color: "#111111",
      // distortion,
      // ...inoptions,
    };
    super(options.container);
    this.options = options;

    this.camera.position.z = 10;
    this.camera.position.y = 0;
    this.camera.rotation.z = 0;
    // this.camera.position.x = 0;

    // const helper = new THREE.CameraHelper( this.camera );
    // this.cameraHelper = helper;
    // this.scene.add( helper );

    this.modifiers = new Modifiers();
    this.ui = {
      bs: document.getElementById("bs"),
      bstext: document.querySelector("#bs .text")
    }

    let colors1 = ["#ff0000", "#00ff00", "#0000ff"];
    let colors2 = ["#ff0000", "#00ff00", "#0000ff"];

    colors1 = ["#fbc50b", "#de5d00", "#f9f5d6"];
    colors2 = ["#eb6333", "#b1d5de", "#182c37"];

    colors1 = ["#0CECDD", "#FFF338", "#FF67E7", "#C400FF"];
    colors2 = ["#dbdcad", "#2FA4FF", "#00FFDD", "#E8FFC2"];

    let padel = new Padel(this);
    padel.init();
    this.padel = padel;

    this.ball = new Ball(this);
    this.ball.init();

    this.overlay = new Overlay(this);
    this.overlay.init();

    this.bricks = [];
    this.points_record = 0;

    this.hits = 0;
    this.hits_last = 0;
    this.hits_rate = 0;

    const rows = 6;
    const columns = 10;

    const brickPoints = [
      1,1,1,1,
      2,2,2,
      3,3,
      4,4];

    for (let row = 0; row < rows; row++) {
      for (let column = 0; column < columns; column++) {
        const brick = new Brick(this, brickPoints, rows, columns, row, column);
        brick.init();
        this.bricks.push(brick);
      }
    }

    this.scene.background = new THREE.Color("pink");
    const axesHelper = new THREE.AxesHelper(5);
    this.scene.add(axesHelper);
  }

  init() {
    this.points = 0;
    this.points_record = localStorage.getItem("points_record");
    this.updateScores();
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enabled = false;

    this.tick();


  }

  updateScores() {
    document.getElementById("last_score").innerText = this.points_record;
    document.getElementById("score").innerText = this.points;
  }

  hitsFloor() {    
    return this.ball.mesh.position.y < -worldHeight / 2 + 10;
  }

  updateBs(time, isbslow) {
    // every 2 second
    var bs  =this.modifiers.getBloodSugar(time);
    this.ui.bstext.innerText = bs.toFixed(1);

    if(isbslow) {
      this.overlay.show();
      howls.forEach(s => s.rate(.7));
      this.ui.bs.classList.add("low");      
    } else {
      this.overlay.hide();
      howls.forEach(s => s.rate(1.0));
      this.ui.bs.classList.remove("low");
    }

    const bsnormal = this.modifiers.isBloodSugarNormal();
    if(bsnormal) {
      this.ui.bs.classList.add("normal");
    } else {
      this.ui.bs.classList.remove("normal");
    }

    const bshigh = this.modifiers.isBloodSugarHigh();
    if(bshigh) {
      this.ui.bs.classList.add("high");
    } else {
      this.ui.bs.classList.remove("high");
    }


  }

  update(delta) {
    let time = this.clock.elapsedTime;
    this.modifiers.update(time);
    
    const isbslow = this.modifiers.isBloodSugarLow();

    this.updateBs(time, isbslow);
    this.overlay.update(time)
    
     this.controls.update()
    
    

    // every 2 second
    if (time % 2 === 0) {
      this.hits_rate = Math.max(1, this.hits - this.hits_last);
    }

    
    this.padel.update(time, isbslow);
    this.ball.update(time);
    this.bricks.forEach((b) => {
      b.update(time);
    });

    if (this.hitsFloor()) {
      // DIE AND RESET GAME
      this.ball.mesh.material.color = new THREE.Color("#000000");
      const myPoints = this.points;
      if (myPoints > this.points_record) {
        console.log("score", myPoints, this.points_record);
        this.points_record = myPoints;
        localStorage.setItem("points_record", this.points_record);
        this.updateScores();
      }

      this.clock = new THREE.Clock();

      // DEATH
      setTimeout(() => {
        this.points = 0;
        this.ball.mesh.material.color = new THREE.Color("#ff0000");
        this.updateScores();
        this.reviveAll();
      }, 1000);
    }
    //this.camera.rotateOnAxis(new THREE.Vector3(0, 1, 0), 0.00001);
    //this.camera.rotateZ(.001);


    // BOUNCE ON PADEL
    if (this.isBallAlignedWithPaddle() && this.ball.vel.y < 0) {

      // if ball hits same side of paddle
      // console.log(this.ball.mesh.position.x, this.padel.mesh.position.x);
      // if ball is coming from left and hits left side of paddle
      
      // if (this.ball.vel.x > 0 && this.ball.mesh.position.x > this.padel.mesh.position.x) {
      //   this.ball.vel.reflect(new Vector3(0, 1, 0));        
      // }
      // else if(this.ball.vel.x < 0 && this.ball.mesh.position.x < this.padel.mesh.position.x) {
      //   this.ball.vel.reflect(new Vector3(0, 1, 0));        
      // }
      if(this.padel.vel.x < 0 && this.ball.vel.x > 0) {
        this.ball.vel.negate()
      } else if(this.padel.vel.x > 0 && this.ball.vel.x < 0) {
        this.ball.vel.negate()
      }
      else {
        this.ball.vel.reflect(new Vector3(0, 1, 0));
      }


      this.padel.hit();
      this.ball.bounce();
      //this.shake = true;
      setTimeout(() => {
        this.shake = false;
      }, 500);
    }

    let brick = this.isBallAlignedWithBrick();
    if (brick) {
      this.ball.hitBrick(brick);
      this.ball.vel.reflect(new Vector3(0, 1, 0));

      var points = brick.hit();

      if (points > 0) {
        this.hits++;

        setTimeout(() => {
          brick.revive();
        }, 40000);
        this.points += points;
        this.shake2 = true;
        setTimeout(() => {
          this.shake2 = false;
        }, 200);
      }
      document.getElementById("score").innerText = this.points;
    }

    if (this.shake) {
      this.camera.rotation.z = Math.sin(time * 100) * 0.02;
    } else if (this.shake2) {
      this.camera.rotation.z = Math.sin(time * 100) * 0.005;
    } else {
      this.camera.rotation.z = 0;
    }

    // console.log(this.isBallAlignedWithPaddle());
  }

  reviveAll() {
    this.bricks.forEach((brick) => {
      brick.revive();
    });
  }

  isBallAlignedWithBrick = () => {
    const ballX = this.ball.mesh.position.x;
    const ballY = this.ball.mesh.position.y;

    return this.bricks
      .filter((b) => b.life)
      .find((brick) => {
        const paddleX = brick.mesh.position.x;
        const halfPaddleWidth = brick.mesh.geometry.parameters.width / 2;

        const paddleY = brick.mesh.position.y;
        const halfPaddleHeight = brick.mesh.geometry.parameters.height / 2;

        const isXAligned =
          ballX > paddleX - halfPaddleWidth &&
          ballX < paddleX + halfPaddleWidth;
        const isYAligned =
          ballY < paddleY + halfPaddleHeight &&
          ballY > paddleY - halfPaddleHeight;

        if (isXAligned && isYAligned) {
          return brick;
        }

        return false;
      });
  };
  isBallAlignedWithPaddle() {
    const ballX = this.ball.mesh.position.x;
    const paddleX = this.padel.mesh.position.x;
    const halfPaddleWidth = this.padel.mesh.geometry.parameters.width / 2;

    const ballY = this.ball.mesh.position.y;
    const paddleY = this.padel.mesh.position.y;
    const halfPaddleHeight = this.padel.mesh.geometry.parameters.height / 2;

    const bonusZone = this.padel.mesh.geometry.parameters.height;

    const isXAligned = ballX > paddleX - halfPaddleWidth -bonusZone && ballX < paddleX + halfPaddleWidth + bonusZone;
    const isYAligned = ballY < paddleY + halfPaddleHeight && ballY > paddleY;
    return isXAligned && isYAligned;
  }
}


const a = new App();
a.init();
