import * as THREE from "three";
import { worldWidth, worldHeight } from "./config";

  

export class Padel {
  constructor(app) {
    this.app = app;
    this.vel = new THREE.Vector3(0, 0, 0);
  }

  init() {
    const padelHeight = 5;
    var geometry = new THREE.BoxGeometry(200, 50, 10);
    var material = new THREE.MeshBasicMaterial({
      color: new THREE.Color("#56cc7d"),
    });
    var mesh = new THREE.Mesh(geometry, material);
    //cube.position.z = 15;
    mesh.position.y = -worldHeight / 2 + padelHeight / 2;
    mesh.position.x = 0;
    this.mesh = mesh;

    this.app.scene.add(mesh);

    // listen on arrow left and right
    let pressed = [];
    const updateDirection = () => {
      const key = pressed[0];
      if (key == "ArrowLeft") {
        this.vel.x = -1;
      } else if (key == "ArrowRight") {
        this.vel.x = 1;
      } else {
        this.vel.x = 0;
      }
    };
    window.addEventListener("keydown", (e) => {
      pressed.unshift(e.key);
      updateDirection();
    });

    window.addEventListener("touchmove", (e) => {
      console.log(e);
      var currentX = e.touches[0].clientX;
      //this.mesh.position.x = Math.min(currentX - window.innerWidth / 2, window.innerWidth / 2);
    });

    // stop when key is released
    window.addEventListener("keyup", (e) => {
      pressed = pressed.filter((key) => key !== e.key);
      updateDirection();
    });
  }

  hit() {
    //sound.play();
    this.mesh.material.color.set(new THREE.Color("lightBlue"));
    //this.mesh.scale.x = 1.4;
    this.mesh.scale.y = 0.6;
    setTimeout(() => {
      this.mesh.material.color.set(new THREE.Color("#56cc7d"));
      //this.mesh.scale.x = 1.0;
      this.mesh.scale.y = 1;
    }, 100);
  }

  update(time, isbslow) {
    let speed = 10.0;
    let vel = this.vel;
    let wOffset = 200 / 2;
    let xPos = this.mesh.position.x;

    if (isbslow) {
      this.mesh.scale.x = .6;
      this.mesh.material.color = new THREE.Color("#ff0000");
    } else {
      this.mesh.scale.x = 1;
      this.mesh.material.color = new THREE.Color("#56cc7d");
    }


    // bounce
    // if(xPos + wOffset > worldWidth/2 || this.cube.position.x - wOffset < -worldWidth/2) {
    //   vel.negate();
    // }
    let movement = vel.clone().multiplyScalar(speed);
    // stop
    if (xPos + wOffset > worldWidth / 2 || xPos - wOffset < -worldWidth / 2) {
      //movement.set(0,0,0);
    }

    this.mesh.position
      .add(movement)
      .clamp(
        new THREE.Vector3(-worldWidth / 2 + wOffset, -worldHeight),
        new THREE.Vector3(worldWidth / 2 - wOffset, worldHeight)
      );
  }
}
