import * as THREE from "three";
import gsap from "gsap";
import { shader as overlayfrag } from "./overlay.frag";

import { worldWidth, worldHeight } from "./config";

export class Overlay {
  constructor(app) {
    this.app = app;
  }

  init() {
    var geometry = new THREE.PlaneGeometry(worldWidth, worldHeight);
    // const texture = loader.load(textureUrl)
    const brickvert = [
      "varying vec3 vPositionW;",
      "varying vec3 vNormalW;varying vec2 vUv;",

      "void main() {",
      " vUv = uv;",
      "	vPositionW = vec3( vec4( position, 1.0 ) * modelMatrix);",
      " vNormalW = normalize( vec3( vec4( normal, 0.0 ) * modelMatrix ) );",

      "	gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );",

      "}",
    ].join("\n");
    var material = new THREE.ShaderMaterial({
      color: new THREE.Color("#ffffff"),
      fragmentShader: overlayfrag,
      vertexShader: brickvert,
      transparent: true,
      uniforms: {
        uReslution: new THREE.Uniform(new THREE.Vector2(worldWidth, worldHeight)),
        uColor: new THREE.Uniform(new THREE.Color("#000000")),
        uAlpha: new THREE.Uniform(0.0),
        uTime: new THREE.Uniform(0),
      },
    });

    this.mesh = new THREE.Mesh(geometry, material);
    // Put close to camera
    this.mesh.position.z = 10;
    this.app.scene.add(this.mesh);
    this.isShown = false;
  }

  show() {
    if (!this.isShown) {
      this.isShown = true;
      gsap.to(this.mesh.material.uniforms.uAlpha, { duration: 0.5, value: .7 });
    }

  }

  hide() {
    if (this.isShown) {
      gsap.to(this.mesh.material.uniforms.uAlpha, {duration: 0.5, value: 0.0})  
      this.isShown = false;
    }
  }

  update(time) {
    this.mesh.material.uniforms.uTime.value = time;
  }
}
