import * as THREE from "three";

const loader = new THREE.TextureLoader();
import smokeUrl from "./smoke1.png";
export var smoke1 = loader.load(smokeUrl);
import smokeUrl2 from "./smoke2.png";
export var smoke2 = loader.load(smokeUrl2);

import { worldWidth, worldHeight } from "./config";



export class Ball {
  constructor(app) {
    this.app = app;
    this.vel = new THREE.Vector3(1, 1, 0);
  }

  hitBrick(brick) {

    this.mesh.material.color =
      brick.mesh.material.uniforms.uColor.value.clone();
    this.mesh.material.color.multiplyScalar(1);

    // smoke
    const smokeOffset = Math.random() * 20;

    this.smoke1.material.rotation = 10 * Math.random();
    this.smoke1.scale.set(40 - smokeOffset, 40 - smokeOffset, 1);
    this.smoke1.position.copy(this.mesh.position);
    this.smoke1.position.z = 5;
    this.smoke1.material.color = brick.mesh.material.uniforms.uColor.value
      .clone()
      .multiplyScalar(1.6);

    setTimeout(() => {
      this.smoke1.scale.set(0, 0, 0);
    }, 100);
  }

  init() {
    var geometry = new THREE.SphereGeometry(10, 32, 32);
    var material = new THREE.MeshBasicMaterial({
      color: new THREE.Color("red"),
      wireframe: true,
    });
    var mesh = new THREE.Mesh(geometry, material);
    //cube.position.z = 15;
    mesh.position.y = 60;
    mesh.position.x = 0;
    mesh.position.z = 0;
    this.mesh = mesh;

    // const cubeHelper = new THREE.BoxHelper(mesh, 0xffff00);
    // this.app.scene.add(cubeHelper);
    const smokematerial = new THREE.SpriteMaterial({ map: smoke1 });
    this.smoke1 = new THREE.Sprite(smokematerial);
    this.smoke1.scale.set(40, 40, 1);
    this.app.scene.add(this.smoke1);

    const smokematerial2 = new THREE.SpriteMaterial({ map: smoke2 });
    this.smoke2 = new THREE.Sprite(smokematerial2);
    this.app.scene.add(this.smoke2);
    this.app.scene.add(mesh);
  }

  bounce() {
    const smokeOffset = Math.random() * 20;
    this.smoke2.material.rotation = 10 * Math.random();
    this.smoke2.scale.set(40 - smokeOffset, 40 - smokeOffset, 1);
    this.smoke2.position.copy(this.mesh.position);
    this.smoke2.position.z = 5;

    setTimeout(() => {
      this.smoke2.scale.set(0, 0, 0);
    }, 100);
  }

  update(time) {
    let speed = Math.min(5.0 + time * 0.04, 11);
    
    
    let vel = this.vel;
    let wOffset = 10 / 2;
    let hOffset = 10 / 2;
    let xPos = this.mesh.position.x;
    let yPos = this.mesh.position.y;

    // bounce on edges
    if (xPos + wOffset > worldWidth / 2 ||
      this.mesh.position.x - wOffset < -worldWidth / 2) {
      vel.reflect(new THREE.Vector3(1, 0, 0));
      this.bounce();
    }
    if (yPos + hOffset > worldHeight / 2 || yPos - hOffset < -worldHeight / 2) {
      vel.reflect(new THREE.Vector3(0, 1, 0));
      this.bounce();
    }

    let movement = vel.clone().multiplyScalar(speed);
    console.log(movement);
    // stop
    if (xPos + wOffset > worldWidth / 2 || xPos - wOffset < -worldWidth / 2) {
      //movement.set(0,0,0);
    }

    this.mesh.position.add(movement);
  }
}
