import * as THREE from "three";
import { shader as brickfrag } from "./brick.frag";
import { shader as brickvert } from "./brick.vert";
import { worldWidth, worldHeight } from "./config";

import { howls } from "./sounds";
import { pickRandom } from "./utils";

export class Brick {
  constructor(app, brickPoints, rows, columns, row, column) {
    this.app = app;
    this.brickPoints = brickPoints;
    this.rows = rows;
    this.columns = columns;
    this.row = row;
    this.column = column;
  }

  init() {
    const cstep = worldWidth / 10;
    const rstep = (worldHeight / 2) * 3;
    const bheight = 15;
    const xpadding = 10;

    this.points = pickRandom(this.brickPoints);
    this.life = this.points;

    let colors = ["", "#fbff80", "#cc6cf5", "#80c6ff", "orange"];

    let color = colors[this.points];

    var geometry = new THREE.BoxGeometry(cstep - xpadding, bheight, 10);
       

    var material = new THREE.ShaderMaterial({
      fragmentShader: brickfrag,
      vertexShader: brickvert,
      uniforms: {
        uColor: new THREE.Uniform(new THREE.Color(color)),
        uAlpha: new THREE.Uniform(1.0),
        uTime: new THREE.Uniform(0.0),
      },
      transparent: true,
      opacity: 1,
    });
    var mesh = new THREE.Mesh(geometry, material);
    //cube.position.z = 15;
    // depending on
    mesh.position.y = worldHeight / 2 - bheight / 2 - 50;
    mesh.position.y -= bheight * (this.row + 1) + 10 * this.row;

    mesh.position.x = -worldWidth / 2 + cstep * this.column;
    this.mesh = mesh;
    // console.log(mesh.position.y);
    this.app.scene.add(this.mesh);
  }

  hit() {
    this.life--;
    this.mesh.rotateZ(Math.random() * 0.2);
    pickRandom(howls).play();

    if (!this.isActive()) {
      this.mesh.material.uniforms.uAlpha.value = 0.0;
      return this.points;
    }
  }

  isActive() {
    return this.life > 0;
  }

  revive() {
    this.life = this.points;
    this.mesh.material.uniforms.uAlpha.value = 1.0;
    this.mesh.rotation.z = 0;
  }

  update(time) {
    this.time = time;
    this.mesh.material.uniforms.uTime.value = time;
    if (this.life < this.points) {
    }
  }
}
