import * as THREE from "three";

// import * as Stats from "stats.js";
// var stats = new Stats();
// document.body.appendChild( stats.dom );
// stats.showPanel(0);

export class BasicThreeDemo {
    constructor(container) {
      this.container = container;
      this.renderer = new THREE.WebGLRenderer({
        antialias: true
      });
      this.renderer.setSize(container.offsetWidth, container.offsetHeight, false);
      this.renderer.setPixelRatio(window.devicePixelRatio);
  
      container.append(this.renderer.domElement);
  
      // this.camera = new THREE.PerspectiveCamera(
      //   75,
      //   container.offsetWidth / container.offsetHeight,
      //   0.1,
      //   10000
      // );
      let width = container.offsetWidth;
      let height = container.offsetHeight;
      width = 100 * container.offsetWidth / container.offsetHeight;
      height = 100 * container.offsetWidth / container.offsetHeight;

      const aspectRatio = container.offsetWidth / container.offsetHeight;
      const cameraWidth = 1000;
      const cameraHeight = cameraWidth / aspectRatio;

      this.camera = new THREE.OrthographicCamera(cameraWidth / - 2, cameraWidth / 2, cameraHeight / 2, cameraHeight / - 2, 0, 1000 );

      

      this.scene = new THREE.Scene();
  
      this.clock = new THREE.Clock();
      this.assets = {};
      this.disposed = false;
      this.tick = this.tick.bind(this);
      this.init = this.init.bind(this);
    }
    loadAssets() {
      return new Promise((resolve, reject) => {
        // const manager = new THREE.LoadingManager(resolve);
        // this.text.load(manager);
      });
    }
    init() {
      this.tick();
    }
    getViewSizeAtDepth(depth = 0) {
      const fovInRadians = (this.camera.fov * Math.PI) / 180;
      const height = Math.abs(
        (this.camera.position.z - depth) * Math.tan(fovInRadians / 2) * 2
      );
      return { width: height * this.camera.aspect, height };
    }
    dispose() {
      this.disposed = true;
    }
    onResize() {}
    update() {}
    render() {
      this.renderer.render(this.scene, this.camera);
    }
    tick() {
      if (this.disposed) return;
      // stats.begin()
      if (resizeRendererToDisplaySize(this.renderer)) {
        const canvas = this.renderer.domElement;
        this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
        this.camera.updateProjectionMatrix();
        this.onResize();
      }
      const delta = this.clock.getDelta();
      this.render(delta);
      this.update(delta);
      // stats.end()
      requestAnimationFrame(this.tick);
    }
  }
  
  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }